<template>
  <div class="user-msg-page">
    <el-breadcrumb class="user-msg-header" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ name: 'Home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>个人信息</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="user-msg-card">
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="账号">
          <span class="form-disable-item">{{form.userid}}</span>
        </el-form-item>
        <el-form-item label="姓名">
          <span class="form-disable-item">{{form.username}}</span>
        </el-form-item>
        <el-form-item label="工号">
          <span class="form-disable-item">{{form.cardId}}</span>
        </el-form-item>
        <el-form-item label="邮箱">
          <span class="form-disable-item">{{form.email}}</span>
        </el-form-item>
        <el-form-item label="电话">
          <span class="form-disable-item">{{form.tel}}</span>
        </el-form-item>
        <el-form-item label="手机">
          <span class="form-disable-item">{{form.ph}}</span>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="form.pass" autocomplete="off" class="form-password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="form.checkPass" autocomplete="off" class="form-password"></el-input>
          </el-form-item>
        <el-form-item class="form-submit">
          <el-button type="primary" @click="onSubmit">提交</el-button>
<!--          <el-button>取消</el-button> -->
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.form.checkPass !== '') {
          this.$refs.form.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      form: {
        userid: 'yangwc',
        username: '杨文成',
        cardId: 'A04115',
        email: 'yangwc@sznari.com',
        tel: '0755-12345678',
        ph: '13760621449'
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit () {
      console.log('submit!')
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/css/common.scss';
  .user-msg-page {
    background-color: #f2f6fc;
    min-height: px2rem(520);
    overflow: hidden;
    padding: px2rem(20) 0 px2rem(20) px2rem(35);
    .user-msg-header {
      margin: 0 0 px2rem(20) px2rem(50);
    }
    .user-msg-card {
      width: 60%;
      margin: px2rem(20) auto 0;
    }
    .form-disable-item {
      display: inline-block;
      text-indent: px2rem(16);
    }
    .el-form-item {
      margin-bottom: px2rem(10);
    }
    .form-password {
      width: 60%;
    }
    .form-submit {
      margin-top: px2rem(30);
      margin-left: px2rem(20);
    }
  }
</style>



