<template>
  <el-container>
      <!--顶部-->
      <el-header class="header" :style="'background-color:' + headerColor">
          <el-row :gutter="24">
            <el-col :span="5" :style="'background-color:' + headerColor">
              <div class="grid-content bg-purple">
                <p v-if="$portalMsg.header.logo.type === 'string'" class="portal-name">{{$portalMsg.header.logo.value}}</p>
                <img v-if="$portalMsg.header.logo.type === 'image'" :src="$portalMsg.header.logo.value" alt="">
              </div>
            </el-col>
            <el-col :span="15" style="height: 30px;">
            </el-col>
            <el-col :span="4">
              <div class="grid-content bg-purple">
                <el-row :gutter="24">
                  <el-col :span="4" style="height: 30px;">
                  </el-col>
                  <el-col :span="5" class="color-box" :style="!$portalMsg.header.allowChangeColor ? 'height: 30px;' : ''">
                    <el-dropdown v-if="$portalMsg.header.allowChangeColor" class="change-color">
                      <span class="el-dropdown-link">
                        <span class="el-icon-picture-outline-round"></span>
                      </span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item><span>头部 --</span><el-color-picker v-model="headerColor" size="mini" class="color-picker"></el-color-picker></el-dropdown-item>
                        <el-dropdown-item><span>菜单 --</span><el-color-picker v-model="menuColor" size="mini" class="color-picker"></el-color-picker></el-dropdown-item>
                        <!-- <el-dropdown-item><span>LOGO --</span><el-color-picker v-model="logoColor" size="mini" class="color-picker"></el-color-picker></el-dropdown-item> -->
                        <!-- <el-dropdown-item><span>主题 --</span><el-color-picker v-model="mainColor" size="mini" class="color-picker"></el-color-picker></el-dropdown-item> -->
                      </el-dropdown-menu>
                    </el-dropdown>
                  </el-col>
                  <el-col :span="3">
                      <div><span class="el-icon-user user-name-icon"></span></div>
                  </el-col>
                  <el-col :span="12">
                      <el-dropdown @command="handleCommand" class="user-msg-box">
                        <span class="el-dropdown-link">
                          <span class="user-name">yangwc</span><i class="el-icon-arrow-down el-icon--right user-icon-right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                          <el-dropdown-item command="userMsg"><span class="el-icon-user-solid"></span> &nbsp;个人信息</el-dropdown-item>
                          <el-dropdown-item command="email"><span class="el-icon-message"></span> &nbsp;我的邮箱</el-dropdown-item>
                          <el-dropdown-item command="backlog"><span class="el-icon-document"></span> &nbsp;我的待办</el-dropdown-item>
                          <el-dropdown-item command="home"><span class="el-icon-s-home"></span> &nbsp;回到主页</el-dropdown-item>
                          <el-dropdown-item command="logout" divided><span class="el-icon-bottom-left"></span> &nbsp;退出登录</el-dropdown-item>
                        </el-dropdown-menu>
                      </el-dropdown>
                  </el-col>
                </el-row>
              </div>
            </el-col>
          </el-row>
      </el-header>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      headerColor: this.$utils.cookie('headerColor') || this.$portalMsg.header.color || '#465161' || '#35B0CF',
      logoColor: '#409EFF',
      menuColor: this.$utils.cookie('menuColor') || this.$portalMsg.menu.color || '#807878' || '#616C77'
    }
  },
  watch: {
    headerColor (newVal, oldVal) {
      this.$utils.cookie('headerColor', newVal, {
        'expires': 365
      })
    },
    menuColor (newVal, oldVal) {
      this.$emit('getMenuColor', newVal)
      this.$utils.cookie('menuColor', newVal, {
        'expires': 365
      })
    }
  },
  methods: {
    handleCommand (command) {
      if (command === 'logout') {
        this.$router.push({
          name: 'Login'
        })
      } else if (command === 'home') {
        this.$router.push({
          name: 'Home'
        })
      } else if (command === 'userMsg') {
        this.$router.push({
          name: 'UserMsg'
        })
      } else if (command === 'email') {
        this.$router.push({
          name: 'LinkPage',
          params: {
            link: encodeURIComponent('http://mail.sznari.com/mail/yangwc.nsf/MainFrameset?OpenFrameSet')
          }
        })
      } else if (command === 'backlog') {
        this.$router.push({
          name: 'LinkPage',
          params: {
            link: encodeURIComponent('http://oa.sznari.com/weboa/common/todo.nsf/mainframeset?openpage')
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/css/common.scss';

  .header {
    color: #fff;
    line-height: px2rem(58);
    .portal-name {
      font-size: px2rem(20);
    }
    .user-name-icon {
      font-size: px2rem(20);
      display: inherit;
      line-height: px2rem(60);
    }
    .user-msg-box {
      height: px2rem(40);
    }
    .user-name, .user-icon-right {
      color: #fff;
      cursor: pointer;
    }
    .el-icon-picture-outline-round {
      color: #fff;
      font-size: px2rem(20);
      cursor: pointer;
    }
    .color-box {
      line-height: px2rem(66);
      .change-color {
        margin: 0 px2rem(10);
        height: px2rem(40);
      }
    }
  }
  .color-picker {
    vertical-align: middle;
  }
</style>



