<template>
  <div class="login-page" :style="'background:' + $portalMsg.login['backgrond-color']">
    <div class="login-center" :class="$portalMsg.login['login-box-position'] === 'right' ? 'box-right' : ($portalMsg.login['login-box-position'] === 'left' ? 'box-left' : 'box-center')">
      <div class="login-header text-center">
        <p>{{$portalMsg.login['portal-name']}}</p>
      </div>
      <form class="el-form">
        <div class="el-form-item">
          <div class="el-form-item__content">
            <div class="el-input">
              <span class="el-icon-user form-icon"></span>
              <input type="text" autocomplete="off" class="el-input__inner" placeholder="请输入用户名" v-model="userNameValue">
            </div>
          </div>
        </div>
        <div class="el-form-item">
          <div class="el-form-item__content">
            <div class="el-input">
              <span class="el-icon-lock form-icon"></span>
              <input type="password" autocomplete="off" class="el-input__inner" placeholder="请输入密码" v-model="passwordValue">
            </div>
          </div>
        </div>
        <div v-if="$portalMsg.login['has-verification-code']" class="el-form-item">
          <div class="el-form-item__content">
            <div class="el-input">
              <span class="el-icon-check form-icon"></span>
              <input type="text" autocomplete="off" class="el-input__inner validCodeInput" placeholder="请输入验证码" v-model="validCodeValue">
              <verification-code :value.sync="validCode"></verification-code>
            </div>
          </div>
        </div>
        <div>
          <button type="button" class="el-button el-button--primary login-btn" @click="loginFn">
            <span>立即登录</span>
          </button>
        </div>
      </form>
      <hr>
      <footer class="text-center footer">
        <p >Copyright © 2020 {{$portalMsg.login['company-name']}}</p>
      </footer>
    </div>
  </div>
</template>

<script>
import verificationCode from './verification-code.vue'
export default {
  data () {
    return {
      userNameValue: '',
      passwordValue: '',
      validCodeValue: '',
      validCode: ''
    }
  },
  created () {

  },
  methods: {
    // 表单验证
    verifyForm () {
      if (!this.userNameValue) {
        this.$message({
          message: '用户名不能为空',
          center: true,
          showClose: true
        })
        return false
      } else if (!this.passwordValue) {
        this.$message({
          message: '密码不能为空',
          center: true,
          showClose: true
        })
        return false
      } else if (this.$portalMsg.login['has-verification-code'] && !this.validCodeValue) {
        this.$message({
          message: '验证码不能为空',
          center: true,
          showClose: true
        })
        return false
      } else if (this.$portalMsg.login['has-verification-code'] && this.validCodeValue.toUpperCase() !== this.validCode.toUpperCase()) {
        this.$message({
          message: '验证码不正确',
          center: true,
          showClose: true
        })
        return false
      } else {
        return true
      }
    },
    loginFn () {
      if (this.verifyForm()) {
        // 表单初步检验无误可请求接口登录
        this.$router.push({
          name: 'Home'
        })
      }
    }
  },
  components: {
    verificationCode
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/css/common.scss';

  .login-page {
    height: px2rem(625);
    overflow: hidden;
    .box-center {
      margin: px2rem(110) auto 0;
    }
    .box-left {
      margin: px2rem(110) 0 0 px2rem(100);
    }
    .box-right {
      margin: px2rem(110) auto 0;
      margin-right: px2rem(100);
    }
    .login-header {
      height: px2rem(36);
      margin-bottom: px2rem(20);
      p {
        font-size: px2rem(24);
        font-weight: bold;
        text-align: center;
        color: #aaa;
      }
    }
    .login-center {
      background: #fff;
      width: px2rem(360);
      height: px2rem(320);
      padding: px2rem(30) px2rem(50);
      border-radius: px2rem(5);
      .el-form {
        margin-bottom: px2rem(20);
      }
      .el-form-item {
        margin-bottom: px2rem(15);
      }
      .el-input__inner {
        text-indent: px2rem(20);
      }
      .validCodeInput {
        width: px2rem(260);
      }
      .login-btn {
        width: 100%;
      }
      .form-icon {
        position: absolute;
        top: px2rem(13);
        left: px2rem(13);
      }
      .footer {
        margin-top: px2rem(20);
      }
    }
  }
</style>



