<template>
  <div style="height:auto;" class="link-page">
    <iframe style="width:100%;" :src="linkUrl" frameborder="0" scrolling="no" id="linkIframe">
    </iframe>
  </div>
</template>

<script>
export default {
  name: 'turnoverfamily',
  data () {
    return {
      linkUrl: ''
    }
  },
  watch: {
    $route () {
      this.linkUrl = decodeURIComponent(this.$route.params.link)
    }
  },
  created () {
    this.linkUrl = decodeURIComponent(this.$route.params.link)
  },
  mounted () {
    /**
     * iframe-宽高自适应显示
     */
    const iframe = document.getElementById('linkIframe')
    let height
    if (window.document.documentElement) {
      height = window.document.documentElement.scrollHeight
    } else {
      height = window.document.body.scrollHeight
    }
    iframe.height = height
  }
}
</script>

<style scoped lang="scss">
  @import '../../assets/css/common.scss';
  .link-page {
    background-color: #f2f6fc;
    min-height: px2rem(520);
    overflow: hidden;
  }
</style>



