/**
 * @fileOverview 公用方法, 将被注入到Vue.prototype里面
 *
 */
import $ from 'jquery'

/**
 * cookie
 **/
let cookie = function (name, value, options) {
  if (typeof value !== 'undefined') {
    options = options || {}

    if (value === null) {
      value = ''
      options.expires = -1
    }

    let expires = ''
    let path = ''
    let domain = ''
    let secure = ''

    if (options.expires && (typeof options.expires === 'number' || options.expires.toUTCString)) {
      let date
      if (typeof options.expires === 'number') {
        date = new Date()
        date.setTime(date.getTime() + (options.expires * 24 * 60 * 60 * 1000))
      } else {
        date = options.expires
      }
      expires = '; expires=' + date.toUTCString()
    }

    path = options.path ? '; path=' + options.path : ''
    domain = options.domain ? '; domain=' + options.domain : ''
    secure = options.secure ? '; secure' : ''
    document.cookie = [name, '=', value, expires, path, domain, secure].join('')
  } else {
    let cookieValue = null
    if (document.cookie && document.cookie !== '') {
      let cookies = document.cookie.split(';')
      for (let i = 0; i < cookies.length; i++) {
        let cookie = $.trim(cookies[i])

        if (cookie.substring(0, name.length + 1) === (name + '=')) {
          cookieValue = cookie.substring(name.length + 1)
          break
        }
      }
    }
    return cookieValue
  }
}

/**
 * 日期格式化
 *
 * @method dateFormat
 * @param {Date/String/Number} date 所要格式化的日期
 * @param {String} mask 格式化格式：'yyyy-mm-dd' | 'HH:MM:ss' | 'hh:MM:ss tt'等
 * @param {boolean} utc 是否取UTC时间(默认: false)
 */
let dateFormat = function (date, mask, utc) {
  let token = /d{1,4}|m{1,4}|yy(?:yy)?|([HhMsTt])\1?|[LloS]|'[^']*'|'[^']*'/g
  let pad = function (val, len) {
    val = String(val)
    len = len || 2
    while (val.length < len) {
      val = '0' + val
    }
    return val
  }
  let masks = {
    'default': 'yyyy-mm-dd HH:MM:ss',
    'shortDate': 'm/d/yy',
    'shortTime': 'h:MM TT',
    'mediumTime': 'h:MM:ss TT',
    'longTime': 'h:MM:ss TT Z',
    'isoDate': 'yyyy-mm-dd',
    'isoTime': 'HH:MM:ss',
    'isoDateTime': 'yyyy-mm-dd\'T\'HH:MM:ss',
    'isoUtcDateTime': 'UTC:yyyy-mm-dd\'T\'HH:MM:ss\'Z\''
  }

  if (!(date instanceof Date)) {
    date = date ? new Date(date) : new Date()
  }

  if (isNaN(date)) {
    throw new SyntaxError('invalid date')
  }

  mask = masks[mask] || mask || masks['default']

  let _ = utc ? 'getUTC' : 'get'
  let d = date[_ + 'Date']()
  let m = date[_ + 'Month']()
  let y = date[_ + 'FullYear']()
  let H = date[_ + 'Hours']()
  let M = date[_ + 'Minutes']()
  let s = date[_ + 'Seconds']()
  let L = date[_ + 'Milliseconds']()
  let o = utc ? 0 : date.getTimezoneOffset()
  let flags = {
    d: d,
    dd: pad(d),
    m: m + 1,
    mm: pad(m + 1),
    yy: String(y).slice(2),
    yyyy: y,
    h: H % 12 || 12,
    hh: pad(H % 12 || 12),
    H: H,
    HH: pad(H),
    M: M,
    MM: pad(M),
    s: s,
    ss: pad(s),
    l: pad(L, 3),
    L: pad(L > 99 ? Math.round(L / 10) : L),
    t: H < 12 ? 'a' : 'p',
    tt: H < 12 ? 'am' : 'pm',
    T: H < 12 ? 'A' : 'P',
    TT: H < 12 ? 'AM' : 'PM',
    o: (o > 0 ? '-' : '+') + pad(Math.floor(Math.abs(o) / 60) * 100 + Math.abs(o) % 60, 4),
    S: ['th', 'st', 'nd', 'rd'][d % 10 > 3 ? 0 : (d % 100 - d % 10 !== 10) * d % 10]
  }

  return mask.replace(token, function ($0) {
    return $0 in flags ? flags[$0] : $0.slice(1, $0.length - 1)
  })
}

/**
 * money格式化
 *
 * @method moneyFormat
 * @param {String/Number} number 数字
 * @param {Number} places 保留小数位数 (默认: 2)
 * @param {String} symbol 币种符号 (默认: '&yen;')
 * @param {String} thousand 千分位 (默认: ',')
 * @param {String} decimal 小数位 (默认: '.')
 */

let moneyFormat = function (number, places, symbol, thousand, decimal) {
  number = number || 0
  places = !isNaN(places = Math.abs(places)) ? places : 2
  symbol = symbol !== undefined ? symbol : '&yen;'
  thousand = thousand || ','
  decimal = decimal || '.'

  let negative = number < 0 ? '-' : ''
  let i = parseInt(number = Math.abs(+number || 0).toFixed(places), 10) + ''
  let j
  j = (j = i.length) > 3 ? j % 3 : 0

  return symbol + negative + (j ? i.substr(0, j) + thousand : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, '$1' + thousand) + (places ? decimal + Math.abs(number - i).toFixed(places).slice(2) : '')
}

/**
 * 字符串替换
 *
 * @method stringFormat
 * @param {String} str 所要替换的字符串
 * @param {Object/String[]} 替换成的值
 */
let stringFormat = function (str) {
  if (typeof str !== 'string') {
    return str
  }

  let l = arguments.length
  let i = 0
  let reg
  let repalceObj

  if (l === 2 && $.type(arguments[1]) === 'object') {
    repalceObj = arguments[1]
    let key
    for (key in repalceObj) {
      if (repalceObj.hasOwnProperty(key)) {
        reg = new RegExp(key, 'g')
        str = str.replace(reg, function () {
          return repalceObj[key]
        })
      }
    }
  } else {
    repalceObj = arguments
    for (; i < l - 1; i++) {
      reg = new RegExp('\\{' + i + '\\}', 'g')
      str = str.replace(reg, function () {
        return repalceObj[i + 1]
      })
    }
  }
  return str
}

/**
 * 字符串去除空格
 *
 * @method trim
 * @param {String} str 要去除空格的字符串
 * @param {number} type 选择类型 1-所有空格，2-前后空格，3-前空格，4-后空格
 */
let trim = function trim (str, type) {
  switch (type) {
    case 1:return str.replace(/\s+/g, '')
    case 2:return str.replace(/(^\s*)|(\s*$)/g, '')
    case 3:return str.replace(/(^\s*)/g, '')
    case 4:return str.replace(/(\s*$)/g, '')
    default:return str
  }
}

/**
 * History
 * 用法
 * var his = new History('key');  // 参数标示cookie的键值
 * his.add("标题", "要存储的信息", "其他内容")；
 * 得到历史数据 返回的是json数据
 * var data = his.getList();  // [{title:"标题", "link": "存储的信息"}]
 *
 */
function History (key) {
  // 最多6条记录
  this.limit = 6
  // 键值
  this.key = key
  // 数据缓存
  this.jsonData = null
  // 72 小时
  this.cacheTime = 72
  // cookie path
  this.path = '/'
}
History.prototype = {
  constructor: History,
  setCookie: function (name, value, expiresHours, options) {
    options = options || {}
    var cookieString = name + '=' + encodeURIComponent(value)
    // 判断是否设置过期时间
    if (undefined !== expiresHours) {
      var date = new Date()
      date.setTime(date.getTime() + expiresHours * 3600 * 1000)
      cookieString = cookieString + '; expires=' + date.toUTCString()
    }
    var other = [
      options.path ? '; path=' + options.path : '',
      options.domain ? '; domain=' + options.domain : '',
      options.secure ? '; secure' : ''
    ].join('')
    document.cookie = cookieString + other
  },
  getCookie: function (name) {
    // cookie 的格式是个个用分号空格分隔
    var arrCookie = document.cookie ? document.cookie.split('; ') : []
    var val = ''
    var tmpArr = ''
    for (var i = 0; i < arrCookie.length; i++) {
      tmpArr = arrCookie[i].split('=')
      // 去掉空格
      tmpArr[0] = tmpArr[0].replace(' ', '')
      if (tmpArr[0] === name) {
        val = decodeURIComponent(tmpArr[1])
        break
      }
    }
    return val.toString()
  },
  deleteCookie: function (name) {
    this.setCookie(name, '', -1, {'path': this.path})
    // console.log(document.cookie)
  },
  initRow: function (title, link, other) {
    return '{"title":"' + title + '", "link":"' + link + '", "other":"' + other + '"}'
  },
  parse2Json: function (jsonStr) {
    var json = []
    try {
      json = JSON.parse(jsonStr)
    } catch (e) {
      // alert('parse error');return;
      // json = eval(jsonStr)
    }
    return json
  },

  // 添加记录
  add: function (title, link, other) {
    var jsonStr = this.getCookie(this.key)
    // alert(jsonStr); return;
    if (jsonStr !== '') {
      this.jsonData = this.parse2Json(jsonStr)
      // 排重
      for (var x = 0; x < this.jsonData.length; x++) {
        if (link === this.jsonData[x]['link']) {
          // 把重复的放到最前面
          this.jsonData.splice(x, 1)
          // return false
        }
      }
      // 重新赋值 组装 json 字符串
      jsonStr = '[' + this.initRow(title, link, other) + ','
      for (var i = 0; i < this.limit - 1; i++) {
        if (undefined !== this.jsonData[i]) {
          jsonStr += this.initRow(this.jsonData[i]['title'], this.jsonData[i]['link'], this.jsonData[i]['other']) + ','
        } else {
          break
        }
      }
      jsonStr = jsonStr.substring(0, jsonStr.lastIndexOf(','))
      jsonStr += ']'
    } else {
      jsonStr = '[' + this.initRow(title, link, other) + ']'
    }
    // alert(jsonStr);
    this.jsonData = this.parse2Json(jsonStr)
    this.setCookie(this.key, jsonStr, this.cacheTime, {'path': this.path})
  },
  // 得到记录
  getList: function () {
    // 有缓存直接返回
    if (this.jsonData !== null) {
      // Array
      return this.jsonData
    }
    // 没有缓存从 cookie 取
    var jsonStr = this.getCookie(this.key)
    if (jsonStr !== '') {
      this.jsonData = this.parse2Json(jsonStr)
    }

    return this.jsonData
  },
  // 清空历史
  clearHistory: function () {
    this.deleteCookie(this.key)
    this.jsonData = null
  }
}

export default {
  cookie: cookie,
  dateFormat: dateFormat,
  moneyFormat: moneyFormat,
  stringFormat: stringFormat,
  trim: trim,
  History: History
}



