import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login/login.vue'
import Main from '../components/main/main-page.vue'
import Home from '../components/main/home.vue'
import UserMsg from '../components/main/user-msg.vue'
import LinkPage from '../components/main/link-page.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/main',
      name: 'Main',
      component: Main,
      children: [
        {
          path: 'home', name: 'Home', component: Home
        },
        {
          path: 'usermsg', name: 'UserMsg', component: UserMsg
        },
        {
          path: 'linkpage/:link', name: 'LinkPage', component: LinkPage
        }
      ]
    }
  ]
})



