<template>
  <div class="home-page">
    <el-row>
      <el-card class="box-card card bg-primary fl">
        <div class="card-body clearfix" @click="gatobacklog('http://oa.sznari.com/weboa/common/todo.nsf/mainframeset?openpage')">
          <div class="card-right fr">
            <p class="itemName">今日待办数</p>
            <p class="itemNum">102</p>
          </div>
          <div class="fl">
            <span class="img-avatar img-avatar-48 bg-translucent">
              <i class="item-icon el-icon-edit"></i>
            </span>
          </div>
        </div>
      </el-card>
      <el-card class="box-card card bg-primary fl bg-danger">
        <div class="card-body clearfix" @click="gatoemail('http://mail.sznari.com/mail/yangwc.nsf/MainFrameset?OpenFrameSet')">
          <div class="card-right fr">
            <p class="itemName">未读邮件</p>
            <p class="itemNum">10</p>
          </div>
          <div class="fl">
            <span class="img-avatar img-avatar-48 bg-translucent">
              <i class="item-icon el-icon-edit"></i>
            </span>
          </div>
        </div>
      </el-card>
      <el-card class="box-card card bg-primary fl bg-success">
        <div class="card-body clearfix" @click="gatoemail('http://www.baidu.com')">
          <div class="card-right fr">
            <p class="itemName">今日待办数</p>
            <p class="itemNum">102</p>
          </div>
          <div class="fl">
            <span class="img-avatar img-avatar-48 bg-translucent">
              <i class="item-icon el-icon-edit"></i>
            </span>
          </div>
        </div>
      </el-card>
      <el-card class="box-card card bg-primary fl bg-purple">
        <div class="card-body clearfix" @click="gatoemail('https://www.baidu.com')">
          <div class="card-right fr">
            <p class="itemName">今日待办数</p>
            <p class="itemNum">102</p>
          </div>
          <div class="fl">
            <span class="img-avatar img-avatar-48 bg-translucent">
              <i class="item-icon el-icon-edit"></i>
            </span>
          </div>
        </div>
      </el-card>
    </el-row>
    <el-row class="clearfix">
      <el-card class="my-history-card fl">
        <div slot="header" class="clearfix">
          <span class="card-title">常用模块</span>
          <!-- <el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button> -->
        </div>
        <div v-if="historyData.length">
          <el-tag v-for="item in historyData" class="historyItem" @click="gotoLink(item.title, item.link)" :key="item.title" :type="colorArr[Math.floor((Math.random()*colorArr.length))]">
            {{item.title}}
          </el-tag>
        </div>
        <div v-else class="none-data">
          <img src="../../assets/images/none-data1.png" alt="暂无数据">
        </div>
      </el-card>
      <div class="date-box fl">
        <el-calendar v-model="date">
        </el-calendar>
      </div>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      hisdomlength: 0,
      colorArr: ['success', 'info', 'warning', 'danger'],
      historyData: [],
      date: new Date()
    }
  },
  created () {
    this.getHistory()
  },
  methods: {
    // 从cookie获取使用记录
    getHistory () {
      let his = new this.$utils.History('myHistory')
      let data = his.getList()
      // 指定排序规则(使用次数从高到低)
      function sortNumber (o1, o2) {
        return o2['other'] - o1['other']
      }
      if (data) {
        this.hisdomlength = data.length
        data.sort(sortNumber)
        this.historyData = data
      }
    },
    gatobacklog (link) {
      this.$router.push({
        name: 'LinkPage',
        params: {
          link: encodeURIComponent(link)
        }
      })
    },
    gatoemail (link) {
      this.$router.push({
        name: 'LinkPage',
        params: {
          link: encodeURIComponent(link)
        }
      })
    },
    gotoLink (title, link) {
      this.$router.push({
        name: 'LinkPage',
        params: {
          link: encodeURIComponent(link)
        }
      })
      this.addHistory(title, link)
    },
    // 存储使用记录到cookie
    addHistory (title, link) {
      let addNum = this.getHistoryNum(title)
      addNum += 1
      let his = new this.$utils.History('myHistory')
      his.add(title, link, addNum.toString())
    },
    // 获取使用记录次数
    getHistoryNum (str) {
      let his = new this.$utils.History('myHistory')
      let data = his.getList()
      let num = 0
      if (data) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].title === str) {
            num = parseInt(data[i].other)
          }
        }
      }
      return num
    }
  }
}
</script>

<style scoped lang="scss">
  @import '../../assets/css/common.scss';
  .home-page {
    background-color: #f2f6fc;
    min-height: px2rem(520);
    overflow: hidden;
    padding: px2rem(20) 0 px2rem(20) px2rem(35);
    .card {
      width: 22.5%;
      height: px2rem(108);
      margin-right: px2rem(20);
      margin-bottom: px2rem(24);
      background-color: #fff;
      -webkit-box-shadow: 0 px2rem(2) px2rem(3) rgba(0, 0, 0, 0.035);
      box-shadow: 0 px2rem(2) px2rem(3) rgba(0, 0, 0, 0.035);
      cursor: pointer;
    }
    .card-body {
      margin-top: px2rem(5);
      padding: 0 px2rem(10);
    }
    .card-right {
      width: 60%;
    }
    .itemName, .itemNum {
      margin-top: 0;
      color: #fff;
    }
    .itemNum {
      margin-top: px2rem(10);
      font-size: px2rem(24);
    }
    .img-avatar {
      display: inline-block !important;
      width: px2rem(64);
      height: px2rem(64);
      line-height: px2rem(64);
      text-align: center;
      vertical-align: middle;
      -webkit-border-radius: 50%;
      border-radius: 50%;
    }
    .img-avatar-48 {
      width: px2rem(48);
      height: px2rem(48);
      line-height: px2rem(55);
      background-color: rgba(250, 250, 250, 0.175);
    }
    .item-icon {
      font-size: px2rem(24);
    }
    .bg-primary {
      background-color: #33cabb !important;
      color: #fff!important;
    }
    .bg-danger {
      background-color: #f96868 !important;
      color: #fff!important;
    }
    .bg-success {
      background-color: #15c377 !important;
      color: #fff!important;
    }
    .bg-purple {
      background-color: #926dde !important;
      color: #fff!important;
    }
    .my-history-card, .date-box {
      width: 47%;
      height: px2rem(332);
    }
    .date-box {
      -webkit-box-shadow: 0 px2rem(2) px2rem(12) 0 rgba(0,0,0,.1);
      box-shadow: 0 px2rem(2) px2rem(12) 0 rgba(0,0,0,.1);
    }
    .my-history-card {
      margin-right: px2rem(20);
    }
    .card-title {
      font-size: px2rem(16);
    }
    .historyItem {
      margin: 0 px2rem(20) px2rem(20) 0;
      cursor: pointer;
    }
    .none-data {
      text-align: center;
      margin-top: px2rem(20);
      img {
        width: px2rem(150);
      }
    }
  }
</style>



