<template>
  <div>
    <header-page @getMenuColor="getMenuColorFn($event)"></header-page>
    <menu-orizontal v-if="menuType === 'orizontal'" :getMenuColor="menuColor" :getActiveIndex="activeIndex"></menu-orizontal>
    <menu-vertical v-if="menuType === 'vertical'" :getMenuColor="menuColor" @getMenuActive="getMenuActiveFn($event)"></menu-vertical>
  </div>
</template>

<script>
import headerPage from './header.vue'
import menuVertical from './menu-vertical.vue'
import menuOrizontal from './menu-orizontal.vue'
export default {
  data () {
    return {
      menuColor: '',
      menuType: 'vertical',
      activeIndex: ''
    }
  },
  watch: {
    $route (to) {
      const arr = ['Home', 'UserMsg']
      if (arr.indexOf(to.name) >= 0) {
        this.menuType = 'vertical'
      } else {
        this.menuType = 'orizontal'
      }
    }
  },
  created () {
    const arr = ['Home', 'UserMsg']
    if (arr.indexOf(this.$router.history.current.name) >= 0) {
      this.menuType = 'vertical'
    } else {
      this.menuType = 'orizontal'
    }
  },
  methods: {
    getMenuColorFn (color) {
      this.menuColor = color
    },
    getMenuActiveFn (index) {
      this.activeIndex = index
    }
  },
  components: {
    headerPage,
    menuVertical,
    menuOrizontal
  }
}
</script>

<style>
</style>



