<template>
  <div class="menu-page menu-vertical-page">
    <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse"  @select="selectItem" text-color="#fff" :background-color="menuColor">
     <el-radio-group v-model="isCollapse">
        <el-radio-button v-show="!isCollapse" :label="true"><span class="el-icon-caret-left" title="收起"></span></el-radio-button>
        <el-radio-button v-show="isCollapse" :label="false"><span class="el-icon-caret-right" title="展开"></span></el-radio-button>
      </el-radio-group>
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span slot="title">更改/反措/任务书</span>
        </template>
        <el-menu-item-group>
          <el-submenu index="1-1">
            <span slot="title">设计更改</span>
            <el-menu-item index="1-1-1" @click="getMenuItem('设计更改单', 'http://bpm.sznari.com:8080/main2.aspx?menuid=b7610b65e5c24287a19027ed4963be0e')">设计更改单</el-menu-item>
            <el-menu-item index="1-1-2" @click="getMenuItem('更改单明细反馈', 'http://bpm.sznari.com:8080/main2.aspx?menuid=94ba90a1ec6945bc90e96dec531fc3ca')">更改单明细反馈</el-menu-item>
            <el-menu-item index="1-1-3" @click="getMenuItem('更改单工作量基础表', 'http://bpm.sznari.com:8080/main2.aspx?menuid=b9254c507df9439892c6041fe1a9ea53')">更改单工作量基础表</el-menu-item>
          </el-submenu>
          <el-submenu index="1-2">
            <span slot="title">反措</span>
            <el-menu-item index="1-2-1" @click="getMenuItem('反措清单', 'http://bpm.sznari.com:8080/main2.aspx?menuid=9bdade6c0a5141878c7d4c864d33662d')">反措清单</el-menu-item>
            <el-menu-item index="1-2-2" @click="getMenuItem('反措反馈', 'http://bpm.sznari.com:8080/main2.aspx?menuid=7663d012ef6b4c43836782b18c41c781')">反措反馈</el-menu-item>
          </el-submenu>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span slot="title">深瑞智能</span>
        </template>
        <el-menu-item-group>
          <el-submenu index="2-1">
            <span slot="title">外购件管理</span>
            <el-menu-item index="2-1-1" @click="getMenuItem('采购计划审批', 'http://bpm.sznari.com:8080/main2.aspx?menuid=7e98d6b57fec47d2bbf0d629e46d106f')">采购计划审批</el-menu-item>
          </el-submenu>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span slot="title">工程售服模块</span>
        </template>
        <el-menu-item-group>
          <el-submenu index="3-1">
            <span slot="title">暂借品申请</span>
            <el-menu-item index="3-1-1" @click="getMenuItem('暂借品申请', 'http://bpm.sznari.com:8080/main2.aspx?menuid=8bc0eb4a99294b06bca25fdf1a753fa0')">暂借品申请</el-menu-item>
          </el-submenu>
          <el-submenu index="3-2">
            <span slot="title">工程信息查询</span>
            <el-menu-item index="3-2-1" @click="getMenuItem('工程信息查询', 'http://bpm.sznari.com:8080/main2.aspx?menuid=9507b78305a649348e7addf4804544dd')">工程信息查询</el-menu-item>
          </el-submenu>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  props: ['getMenuColor'],
  data () {
    return {
      isCollapse: false,
      menuColor: ''
    }
  },
  watch: {
    getMenuColor (newVal) {
      this.menuColor = newVal
    }
  },
  created () {
    this.menuColor = this.$utils.cookie('menuColor') || this.$portalMsg.menu.color
  },
  methods: {
    handleOpen (key, keyPath) {
      // this.$message('开')
    },
    handleClose (key, keyPath) {
      // this.$message('关')
    },
    selectItem (index, indexPath) {
      this.$emit('getMenuActive', index)
      // this.$emit('getMenuType', 'orizontal')
    },
    getMenuItem (item, link) {
      this.$router.push({
        name: 'LinkPage',
        params: {
          link: encodeURIComponent(link)
        }
      })
      this.addHistory(item, link)
    },
    // 存储使用记录到cookie
    addHistory (title, link) {
      let addNum = this.getHistoryNum(title)
      addNum += 1
      let his = new this.$utils.History('myHistory')
      his.add(title, link, addNum.toString())
    },
    // 获取使用记录次数
    getHistoryNum (str) {
      let his = new this.$utils.History('myHistory')
      let data = his.getList()
      let num = 0
      if (data) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].title === str) {
            num = parseInt(data[i].other)
          }
        }
      }
      return num
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/css/common.scss';
  .menu-vertical-page {
    float: left;
  }
  .el-menu {
    border-right: none;
  }
  .el-menu-vertical-demo {
    min-height: px2rem(565);
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: px2rem(240);
  }
  .el-icon-caret-left, .el-icon-caret-right {
    font-size: px2rem(20);
    color: #fff;
  }
  .el-icon-menu {
    color: #fff;
  }
</style>



